.project-detail-container {
  min-height: 100vh;
  background: #000;
  color: #fff;
  padding-top: 90px;
}

.project-detail-header {
  position: fixed;
  top: 90px;
  left: 0;
  right: 0;
  background: #000;
  z-index: 999;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #333;
}

.back-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.back-button:hover {
  background: #222;
}

.project-tabs {
  display: flex;
  gap: 8px;
  flex: 1;
  justify-content: center;
}

.tab-button {
  background: none;
  border: none;
  color: #999;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 4px;
  transition: all 0.3s ease;
  text-transform: capitalize;
}

.tab-button.active {
  background: #2d2d2d;
  color: #fff;
}

.tab-button:hover {
  color: #fff;
  background: #1a1a1a;
}

.back-to-top {
  background: none;
  border: none;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 4px;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.back-to-top:hover {
  background: #222;
}

.project-detail-content {
  margin-top: 80px;
  padding: 60px 80px;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

.overview-section {
  display: flex;
  gap: 80px;
  align-items: flex-start;
}

.overview-left {
  flex: 1;
  max-width: 600px;
}

.overview-right {
  flex: 1;
  max-width: 700px;
}

.project-main-title {
  font-size: 56px;
  font-weight: 700;
  margin-bottom: 40px;
  line-height: 1.1;
  letter-spacing: -1px;
}

.project-brief {
  margin-bottom: 40px;
}

.project-brief h3,
.info-block h4,
.project-section h4 {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #999;
  margin-bottom: 12px;
}

.project-brief p,
.info-block p,
.project-section p {
  font-size: 16px;
  line-height: 1.7;
  color: #ddd;
}

.project-info-grid {
  display: flex;
  gap: 60px;
  margin-bottom: 40px;
}

.info-column {
  flex: 1;
}

.info-block {
  margin-bottom: 32px;
}

.project-section {
  margin-bottom: 32px;
}

.project-link-button {
  display: inline-block;
  margin-top: 20px;
  padding: 14px 28px;
  background: #fff;
  color: #000;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.project-link-button:hover {
  background: #e0e0e0;
  transform: translateY(-2px);
}

.project-footer {
  margin-top: 60px;
  padding-top: 40px;
  border-top: 1px solid #333;
}

.project-footer p {
  font-size: 15px;
  line-height: 1.8;
  color: #999;
}

.project-images {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.project-images img {
  width: 100%;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
}

.tab-content {
  padding: 40px 0;
}

.tab-content h2 {
  font-size: 42px;
  margin-bottom: 24px;
}

.tab-content p {
  font-size: 18px;
  line-height: 1.8;
  color: #999;
}

/* Mobile Responsive */
@media (max-width: 1024px) {
  .overview-section {
    flex-direction: column;
    gap: 60px;
  }

  .overview-left,
  .overview-right {
    max-width: 100%;
  }

  .project-detail-content {
    padding: 60px 40px;
  }
}

@media (max-width: 768px) {
  .project-detail-header {
    padding: 16px 20px;
    flex-wrap: wrap;
    gap: 12px;
  }

  .project-tabs {
    width: 100%;
    justify-content: flex-start;
    overflow-x: auto;
    padding-bottom: 8px;
  }

  .tab-button {
    font-size: 11px;
    padding: 6px 12px;
    white-space: nowrap;
  }

  .back-to-top {
    display: none;
  }

  .project-detail-content {
    margin-top: 140px;
    padding: 40px 20px;
  }

  .project-main-title {
    font-size: 36px;
  }

  .project-info-grid {
    flex-direction: column;
    gap: 0;
  }
}

@media (max-width: 480px) {
  .project-detail-content {
    padding: 30px 16px;
  }

  .project-main-title {
    font-size: 28px;
  }

  .project-brief p,
  .info-block p,
  .project-section p {
    font-size: 14px;
  }
}

